@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,300;1,400;1,500;1,700&display=swap);
.wrapper{align-items:center;min-height:100vh}

.formWrapper{margin-top:59px;padding:48px 32px;background-color:#FFFFFF;border:1px solid #D9D9D9;box-shadow:0 22px 106px rgba(0,0,0,0.07),0 9.19107px 44.2843px rgba(0,0,0,0.0503198),0 4.91399px 23.6765px rgba(0,0,0,0.0417275),0 2.75474px 13.2728px rgba(0,0,0,0.035),0 1.46302px 7.04911px rgba(0,0,0,0.0282725),0 0.6088px 2.93329px rgba(0,0,0,0.0196802);border-radius:6px;font-family:Inter, BlinkMacSystemFont, 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;width:384px}.form__title{font-weight:500;font-size:20px;line-height:28px;padding-bottom:45px;color:#262626}.form__label{font-size:14px;line-height:22px;color:#262626}.form__label--theme_grey8{color:#595959}.form__question{font-size:12px;line-height:20px;color:#8C8C8C}

.formWrapper{margin-top:59px;padding:48px 32px;background-color:#FFFFFF;border:1px solid #D9D9D9;box-shadow:0 22px 106px rgba(0,0,0,0.07),0 9.19107px 44.2843px rgba(0,0,0,0.0503198),0 4.91399px 23.6765px rgba(0,0,0,0.0417275),0 2.75474px 13.2728px rgba(0,0,0,0.035),0 1.46302px 7.04911px rgba(0,0,0,0.0282725),0 0.6088px 2.93329px rgba(0,0,0,0.0196802);border-radius:6px;font-family:Inter, BlinkMacSystemFont, 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;width:384px}.form__title{font-weight:500;font-size:20px;line-height:28px;padding-bottom:45px;color:#262626}.form__label{font-size:14px;line-height:22px;color:#262626}.form__label--theme_grey8{color:#595959}.form__question{font-size:12px;line-height:20px;color:#8C8C8C}

.articlePreview{background-color:#fff;border-radius:5px;padding:15px;box-shadow:0 22px 106px rgba(0,0,0,0.07),0 9.19107px 44.2843px rgba(0,0,0,0.0503198),0 4.91399px 23.6765px rgba(0,0,0,0.0417275),0 2.75474px 13.2728px rgba(0,0,0,0.035),0 1.46302px 7.04911px rgba(0,0,0,0.0282725),0 0.6088px 2.93329px rgba(0,0,0,0.0196802)}.articlePreview__title{color:#1890FF;margin-bottom:0;font-size:20px;line-height:28px}.avatar{width:100%}.articlePreview__description{display:flex;width:100%;border:none;outline:none;cursor:pointer;padding:0;background-color:inherit}.articlePreview__link{color:inherit;overflow-wrap:break-word}.articlePreview__link:hover{color:inherit}.articlePreview__username{color:rgba(0,0,0,0.85);font-size:18px;line-height:28px;overflow-wrap:break-word}

.favoritesCount{color:rgba(0,0,0,0.75);padding-left:5px;font-size:12px;line-height:22px}.heart--black{color:rgba(0,0,0,0.75)}.heart--red{color:#FF0707}

.articlesWrapper{width:941px;padding-top:26px}.articlesWrapper .ant-pagination-item-link{background-color:#E5E5E5;border:none}

.header__username{color:inherit}.header__avatar{max-width:60px}

.header{width:100%;background-color:#fff;height:80px;color:rgba(0,0,0,0.85);font-size:18px}.header__rightContainer,.header__leftContainer{align-items:center;height:80px}.header__signLink{color:rgba(0,0,0,0.85);font-size:18px}.header__signupBtn{color:#52C41A;border:1px solid #52C41A;border-radius:5px;transition:none;font-size:18px;height:51px;width:109px;display:flex;justify-content:center;align-items:center}.header__signupBtn:hover{border-color:#1890ff}.header__createArcticleBtn{color:#52C41A;border:1px solid #52C41A;border-radius:5px;transition:none;font-size:14px;line-height:22px;height:31px;width:112px;display:flex;justify-content:center;align-items:center}.header__createArcticleBtn:hover{border-color:#1890ff}.header__logoutBtn{color:rgba(0,0,0,0.75);border:1px solid rgba(0,0,0,0.75);border-radius:5px;height:51px;width:109px}.header__username{margin-right:13px}.header__linkHome{color:inherit}

.article{background-color:#fff;padding:15px;box-shadow:0 22px 106px rgba(0,0,0,0.07),0 9.19107px 44.2843px rgba(0,0,0,0.0503198),0 4.91399px 23.6765px rgba(0,0,0,0.0417275),0 2.75474px 13.2728px rgba(0,0,0,0.035),0 1.46302px 7.04911px rgba(0,0,0,0.0282725),0 0.6088px 2.93329px rgba(0,0,0,0.0196802);overflow:auto;width:100%}.article_header{font-size:20px;line-height:28px}.article_title{color:#1890FF;margin-bottom:0}.avatar{width:100%}.article_description{color:rgba(0,0,0,0.5);margin-bottom:25px;font-size:12px;line-height:22px}.article_body{color:rgba(0,0,0,0.75);font-size:20px;line-height:28px}.article_delete{color:#F5222D;border-color:#F5222D}.article_edit{color:#52C41A;border-color:#52C41A}.tag{font-size:12px;line-height:15px;color:rgba(0,0,0,0.5)}

.newArticle{width:938px;background-color:#FFFFFF;margin-top:34px;padding:48px 32px;box-shadow:0 22px 106px rgba(0,0,0,0.07),0 9.19107px 44.2843px rgba(0,0,0,0.0503198),0 4.91399px 23.6765px rgba(0,0,0,0.0417275),0 2.75474px 13.2728px rgba(0,0,0,0.035),0 1.46302px 7.04911px rgba(0,0,0,0.0282725),0 0.6088px 2.93329px rgba(0,0,0,0.0196802);border:1px solid #D9D9D9;border-radius:6px}.newArticle__title{color:#262626;font-family:Roboto, serif;font-size:20px;font-weight:500;line-height:28px}.newArticle__form{width:100%}

.tag_btn{font-family:Roboto, serif;font-size:16px}.tag__btn--blue{color:#1890FF;border-color:#1890FF}.tag__btn--red{color:#F5222D;border-color:#F5222D}.tag__btn--red:hover,.tag__btn--red:focus{color:#F5222D;border-color:#F5222D}.tag__field{color:rgba(0,0,0,0.75);font-size:16px;line-height:24px}

.article__label{font-family:Roboto, serif;font-size:14px;line-height:22px}

body{font-family:Inter, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#E5E5E5;display:flex;justify-content:center}#root{width:100%;height:100%}

